function colors(specifier) {
  const n = (specifier.length / 6) | 0
  const colors = new Array(n)
  let i = 0
  while (i < n) {
    colors[i] = `#${specifier.slice(i * 6, ++i * 6)}`
  }
  return colors
}

function ramp(range) {
  const n = range.length
  return function (t) {
    return range[Math.max(0, Math.min(n - 1, Math.floor(t * n)))]
  }
}

export default ramp(
  colors(
    '',
  ),
)
